export const MOBILE_REG_EXP = /^(13|14|15|17|18|19)[0-9]{9}$/;
export const EMAIL_REG_EXP = /^\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}$/;
export const MOBILE_REG_EXP_NATIONAL = /^\d+$/;
export const PLUS_NUM_REG_EXP = /^[1-9]\d*$/;
export const PLUS_FLOAT_REG_EXP = /^[1-9]\d*.\d*$|^0.\d*[1-9]\d*$/;



